import React from 'react';

import { 
    Col,Row,
    Card
 } from "react-bootstrap";

const ExperienceCard = ({data}) => {
    return ( 
        <Row>
            <Col lg="4" className="pb-5 text-center">
                <img className=" bg-white mb-3" src={data.companylogo} alt=""/>
            </Col>
            <Col className="pb-5 text-left">
            <hr/>
                <div class="d-flex justify-content-between">
                    <h4 class="mb-1">{data.role}</h4>
                    <medium>{data.date}</medium>
                </div>
                <p>
                   {data.project}
                </p>
                <p class="text-muted">
                    {data.l_11}
                </p>
                <p class="text-muted">
                    {data.l_12}
                </p>
                <p class="text-muted">
                    {data.l_13}
                </p>
                <br></br>
                <hr/>
                <div class="d-flex justify-content-between">
                    <h4 class="mb-1">{data.role2}</h4>
                    <medium>{data.date2}</medium>
                </div>
                <p>
                   {data.project2}
                </p>
                <p class="text-muted">
                    {data.l_21}
                </p>
                <p class="text-muted">
                    {data.l_22}
                </p>
                <p class="text-muted">
                    {data.l_23}
                </p>
            </Col>
        </Row>

     );
}
 
export default ExperienceCard;